.align-items {
  &-center {
    align-items: center;
  }
  &-end {
    align-items: flex-end;
  }
}

.justify-content {
  &-center {
    justify-content: center;
  }
  &-between {
    justify-content: space-between;
  }
  &-end {
    justify-content: flex-end;
  }
  &-md {
    @include bp(md, min) {
      &-center {
        justify-content: center;
      }
      &-between {
        justify-content: space-between;
      }
      &-end {
        justify-content: flex-end;
      }
    }
  }
}

.flex {
  &-column-reverse {
    flex-direction: column-reverse;
  }
  &-md-row {
    @include bp(md) {
      flex-direction: row;
    }
  }
}

.stretch {
  @include stretch;
}

.h {
  &-auto {
    height: auto !important;
  }
  &-100 {
    height: 100% !important;
  }
}

.pos {
  &-rel {
    position: relative;
  }
  &-fixed {
    position: fixed;
  }
}

.uppercase {
  text-transform: uppercase;
}

.overflow {
  &-hidden {
    overflow: hidden;
  }
}

.element {
  &-hidden {
    @include element-hidden;
  }
  &-visible {
    @include element-visible;
  }
}

.screen-reader-text {
  @include screen-reader;
}

.vstack {
  @include vstack;
}

.hstack {
  @include hstack;
}

.text {
  &-center {
    text-align: center;
  }
  &-end {
    text-align: end;
  }
}

.preload * {
  transition: none !important;
}

.site-map iframe {
  aspect-ratio: 16/9;
  height: auto;
  width: 100%;
}

.rounded-image {
  border-radius: 100%;
}

.content-box {
  width: 100%;
  margin: auto;
  padding: var(--container-spacer);
  background: var(--color-white);
  color: var(--color-primary);
}

#wpadminbar {
  position: fixed;
  top: auto;
  bottom: 0;
  z-index: 1400;
}

.page-header-image {
  position: relative;
  aspect-ratio: 21/7;
  &::before {
    --icon-size: 100%;
    --icon-color: var(--color-white-muted);
    @include mask-icon(
      url("data:image/svg+xml,%3Csvg width='972' height='570' viewBox='0 0 972 570' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M618.728 0H498.431C416.629 250.049 234.579 455.76 0 570H220.946C410.213 430.196 552.163 230.876 618.728 0Z' fill='%23ECEFF1'/%3E%3Cpath d='M853.708 0C802.782 215.298 698.525 410.224 555.772 570H705.342C833.659 405.032 926.287 210.904 972 0H853.708Z' fill='%23ECEFF1'/%3E%3Cpath d='M677.27 0C615.518 225.683 485.597 423.406 311.167 570H479.582C631.157 413.42 742.632 218.094 796.365 0H677.27Z' fill='%23ECEFF1'/%3E%3C/svg%3E%0A"),
      right
    );
    position: absolute;
    inset: 0;
    z-index: 1;
  }
}

.currency {
  &__item {
    margin: 0;
  }

  &__label {
    display: flex;
    align-items: center;
    min-height: var(--input-height);
  }

  &__button {
    --spacer-y: 0.3em;
    --button-height: 2.5em;
    @include button;
    background: none;
  }

  &-list {
    @include reset-list;
    margin: 0;
    li {
      margin: 0;
      &:not(.active) {
        display: none;
      }
    }
  }
}
